.title {
    font-family: "Lora";
}
.text-l {
    font-size: 48px;
}
.text-m {
    font-size: 18px;
}
.border-x {
    border-radius: 20px;
}
.border-m {
    border-radius: 16px;
}
.border-s {
    border-radius: 10px;
}
.border-dot {
    border-radius: 16px;
    border: 1px dotted $secondary;
}
.width-s {
    width: 120px;
}
.overflow-x {
    overflow-x: scroll;
}
.logo {
    width: 118px;
    height: 90px;
}
.position-logo {
    position: absolute;
    top: 0;
    left: 50px;
}
