body {
    *,
    *::before,
    *::after {
        box-sizing: border-box;
    }
    font-family: "Lora", "Noto Sans", "Tajawal", sans-serif !important;
    a {
        display: block;
        text-decoration: none;
        color: black;
    }
    img {
        display: block;
        width: 100%;
        height: auto;
    }
    input {
        outline: none;
        //     -webkit-appearance: none; /* Safari 和 Chrome，常用於iOS下移除內建樣式 */
        //     -moz-appearance: none; /* FireFox */
    }
}
