.banner {
    background-image: url(../images/banner.png);
    height: 742px;
    background-position: 50% bottom;
    background-repeat: no-repeat;
    background-size: cover;
    @media (max-width: 767px) {
        height: 500px;
    }
}
.d-flex-center {
    display: flex;
    justify-content: center;
    align-items: center;
}
.singleLine {
    white-space: nowrap;
    // 段落不換行
    overflow: hidden;
    text-overflow: ellipsis;
    // 處理文字溢出
}
.multi-paragraph {
    // 處理段落溢出
    white-space: normal;
    display: -webkit-box;
    -webkit-box-orient: vertical;
    -webkit-line-clamp: 3;
    // 限制區塊容器中內容為指定行數
}
