.search {
    display: flex;
    flex: 0 0 1%;
    max-width: 900px;
    align-items: center;
    margin: 0 auto;
    @media (max-width: 768px) {
        max-width: 576px;
    }
    @media (max-width: 576px) {
        max-width: 300px;
    }
}
.search-input {
    background: url(../images/search.svg) 2%/24px no-repeat;
    border: 1px solid #283a2c;
    box-shadow: 0px 2px 0px #283a2c;
    background-color: white;
    width: 600px;
    padding: 20px 0px;
    @media (max-width: 768px) {
        width: 100%;
    }
}
.search-go {
    flex-grow: 1;
    // width: 20%;
    text-align: center;
    padding: 20px 0px;
    border: 1px solid white;
    background: transparent;
    transition: all 0.5s;
    &:hover {
        border: 1px solid $secondary;
        background: $secondary;
    }
    &:active {
        border: 1px solid $primary;
        background: $primary;
    }
}
.search-more {
    // width: 10%;
    // width: 150px;
    flex-grow: 1.5;
    text-align: center;
    padding: 20px 0px;
    border: 1px solid $secondary;
    background: $secondary;
    color: white;
    &:hover {
        border: 1px solid $titleBack;
        background: $titleBack;
        color: black;
        transition: all 0.5s;
    }
    // @media (max-width: 992px) {
    //     width: 40%;
    // }
}
.tag {
    transition: all 0.8s;
    &:hover {
        text-shadow: $secondary 0.1em;
        font-weight: bold;
        background-color: $titleBack;
    }
}
.location-box {
    overflow: hidden;
}
.click-card {
    position: relative;
}
.click-btn {
    // display:none
    opacity: 0;
    position: absolute;
    width: 100%;
    height: 100%;
    top: 0;
    border-radius: 10px;
    background-color: rgba(40, 58, 44, 0.5);
    transition: all 1s;
    &:hover {
        opacity: 1;
    }
    p {
        color: rgba(40, 58, 44, 0.6);
        width: 70px;
        height: 70px;
        border-radius: 50%;
        background-color: rgba(255, 255, 255, 0.5);
        transition: all 0.5s;
        &:hover {
            width: 80px;
            height: 80px;
            border: 2px solid white;
            outline: 1px dashed $secondary;
            outline-offset: -5px;
            background-color: white;
            color: $secondary;
        }
    }
}
.input-group-label > label {
    margin: 0 10px 15px 0;
    @media (max-width: 576px) {
        margin-right: 0;
    }
    min-width: 110px;
    padding: 14px 0;
    text-align: center;
    border: 2px solid #e9e3db;
    border-radius: 15px;
    background-color: white;
    transition: all 0.2s;
    cursor: pointer;
    &:hover {
        color: black;
        border: 2px solid #e0a36d;
    }
}
.active {
    background-color: #e0a36d;
}
.input-group-city > label {
    margin: 0 10px 20px 0;
    @media (max-width: 576px) {
        margin-right: 0;
    }
    min-width: 110px;
    padding: 14px 0;
    text-align: center;
    border: 2px solid #e9e3db;
    border-radius: 15px;
    background-color: white;
    transition: all 0.2s;
    cursor: pointer;
    &:hover {
        color: black;
        border: 2px solid #e0a36d;
    }
}
.input-group-label > input,
.input-group-city > input {
    display: none;
}
.location {
    &-area {
        height: 50px;
        min-width: 180px;
        padding: 14px 0;
        text-align: center;
        border: 2px solid #e9e3db;
        border-radius: 15px;
        background-color: #e9e3db;
        @media (max-width: 767px) {
            margin: 0 auto;
        }
    }
    &-not {
        min-width: 210px;
        height: 50px;
        padding: 14px 0;
        text-align: center;
        background-color: rgba(241, 239, 237, 0.3);
        border-radius: 15px;
        margin: 0 auto;
    }
    &-flex {
        display: flex;
        flex-wrap: wrap;
        @media (max-width: 767px) {
            justify-content: space-around;
        }
    }
}
.search-more-info {
    max-height: 0;
    overflow: hidden;
    margin: 0 auto;
    position: absolute;
    max-width: 100%;
    // transition: max-height 1.1s;
    z-index: 999;
}
.search-height-show .search-show {
    max-height: calc(100vh - 150px);
    // 樣式
    border: 1px solid #283a2c;
    border-radius: 8px;
    background-color: white;
    margin-top: 12px;
    padding: 60px;
    @media (max-width: 767px) {
        padding: 30px;
    }
}
.round-map {
    border: 1px solid black;
    border-radius: 50%;
    width: 40px;
}
// 搜尋下拉式選單
.whereHeight-show .whereInfo {
    max-height: calc(100vh - 150px);
    border: 1px solid #283a2c;
    border-radius: 10px;
    background-color: white;
    margin-top: 12px;
    padding: 30px 60px;
}
.whereTo-show {
    max-height: 0;
    overflow-x: hidden;
    overflow-y: auto;
    position: absolute;
    // right: 0;
    // left: 0;
    // transition: max-height 1.1s;
    z-index: 4;
}
